.Sidedrawer {
    top: 0;
    width: 350px;
    max-width: 100%;
    height: 100vh;
    background-color: #ffffff !important;
    position: fixed;
    z-index: 12;
}

.Sidedrawer_Open {
    -webkit-animation: sidedrawer_show 0.3s ease-out;
            animation: sidedrawer_show 0.3s ease-out;
}

@-webkit-keyframes sidedrawer_show {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@keyframes sidedrawer_show {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

.Sidedrawer_Close {
    -webkit-animation: sidedrawer_close 0.33s ease-in;
            animation: sidedrawer_close 0.33s ease-in;
}

@-webkit-keyframes sidedrawer_close {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }
}

@keyframes sidedrawer_close {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }
}

